'use client';

import { createContext, useState, useContext } from 'react';

type MobileMenuContextType = {
  isMobileMenuOpen: boolean;
  toggleMobileMenu: () => void;
};

const MobileMenuContext = createContext<MobileMenuContextType | undefined>(
  undefined
);

type MobileMenuProviderProps = {
  children: React.ReactNode;
};

export const MobileMenuProvider = ({ children }: MobileMenuProviderProps) => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen((prev) => !prev);
  };

  return (
    <MobileMenuContext.Provider value={{ isMobileMenuOpen, toggleMobileMenu }}>
      {children}
    </MobileMenuContext.Provider>
  );
};

export const useMobileMenu = () => {
  const context = useContext(MobileMenuContext);
  if (!context) {
    throw new Error('useMobileMenu חייב להיות בתוך MobileMenuProvider');
  }
  return context;
};
